import React, { useState, useEffect } from "react";
import "./App.css";
import Quiz from "./components/quiz";
import PostData from "./questions.json";
import Home from "./components/Home";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  const [questions, setQuestions] = useState([]);
  const [options, setOptions] = useState([]);
  const [nextquestion, setNextquestion] = useState(0);
  const [correctanswer, setCorrectanswer] = useState(0);

  useEffect(() => {
    getData();
  }, [nextquestion]);

  const getData = async () => {
    setQuestions(PostData.results[nextquestion]);
    setOptions(PostData.results[nextquestion].options);
  };

  // const checkAnswer = (name) => {
  //   alert(name);
  // };
  const c_Answer = PostData.results[nextquestion].correct_answer;

  const sayHello = (ans) => {
    var answer = ans;
    if (answer === c_Answer) setCorrectanswer(correctanswer + 1);
    const value = nextquestion;
    if (value < 14) {
      setNextquestion(nextquestion + 1);
    } else {
      document.getElementById("score").style.display = "block";
      document.getElementById("no").innerHTML = correctanswer;
      if (correctanswer <= 5) {
        document.getElementById("comment").innerHTML =
          'Shame on you <i class="far fa-thumbs-down"></i>';
      } else if (correctanswer > 5 && correctanswer <= 10) {
        document.getElementById("comment").innerHTML =
          'Good try <i class="far fa-thumbs-up"></i>';
      } else {
        document.getElementById("comment").innerHTML =
          'You are a true OP fan! <i class="far fa-surprise"></i>';
      }
    }
  };

  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/quiz">
          <div className="App">
            <img src="img/logo.png" id="logo" alt="logo" />
            <h1>One Piece Quiz</h1>
            <p>Question : {nextquestion + 1}/15</p>
            <Quiz questions={questions} />
            <div className="content">
              {options.map((option) => (
                <div id="col">
                  <button
                    id="option"
                    key={option}
                    value={option}
                    onClick={() => sayHello(option)}
                  >
                    {option}
                  </button>
                </div>
              ))}
            </div>
          </div>
          <div id="score">
            <div className="content">
              <img src="img/logo.png" id="logo" alt="logo" />
              <h1>
                Your score is : <span id="no"></span>/15
              </h1>
              <span id="comment"></span>
            </div>
          </div>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
